}
.toggle-button {
  position: relative;
  display: inline-block;
  width: 40px;
  height: 20px;
  background-color: #ccc;
  border-radius: 15px;
  border: none;
  cursor: pointer;
   padding: 1px 0 1px 1px; 
  transition: background-color 0.3s ease;
}

.toggle-slider {
  display: block;
  width: 45%;
  height: 85%;
  border-radius: 50%;
  background-color: white;
  transition: transform 0.3s ease;
}

.toggle-slider.on {
  transform: translateX(100%);
}

