.NotificationTable,
.reportTable {
  padding: 30px;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
  border-radius: 15px;
  margin: 20px auto;
  width: 100%;
}
.reportTable.lightTheme {
  background-color: white;
}

.reportTable.darkTheme {
  background-color: #1E293B;
}
.NotificationTable h3,
.reportTable h3 {
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
}
.NotificationTable p,
.reportTable p {
  font-size: 14px;
  line-height: 21px;
  font-weight: 500;
}
.ant-table-wrapper {
  margin: 30px 0;
}
.ant-table-cell .ant-tag {
  font-weight: 600;
  color: #991b1b;
  background: #fecaca;
  border-radius: 50px;
  border: none;
}
:where(.css-dev-only-do-not-override-1xb5ddc).ant-card,
:where(.css-dev-only-do-not-override-1xb5ddc).ant-pagination
  .ant-pagination-item-active {
  background-color: rgb(30, 41, 59);
}
:where(.css-dev-only-do-not-override-1xb5ddc).ant-table-wrapper
  .ant-table-thead
  > tr
  > th {
  background-color: rgb(22 34 55);
}
:where(.css-dev-only-do-not-override-1xb5ddc).ant-table-wrapper
  .ant-table-tbody
  > tr
  > td {
  background-color: rgb(30, 41, 59);
}

@media screen and (max-width: 900px) {
  .ant-table-wrapper {
    width: 100%;
    overflow: scroll;
  }
}
