.custom-input {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 200px; 
  border: 1px solid #ccc;
  padding: 4px;
  border-radius: 4px;
}

.custom-input input {
  border: none;
  outline: none;
  flex: 1;
  padding: 0;
}

.calendar-icon {
  cursor: pointer;
  font-size: 1rem; /* Adjust the icon size as needed */
  margin-right: 5px;
  color: #0F172A;
}

/* Add any additional styles for the date picker as needed */
.react-datepicker {
  font-family: Arial, sans-serif;
}

/* Style the date picker calendar */
.react-datepicker-popper {
  z-index: 9999; /* Ensure it appears above other elements */
}

/* Customize the calendar's appearance */
.react-datepicker__month-container {
  border: 1px solid #ccc;
  background-color: #fff;
  border-radius: 4px;
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.2);
}

/* Style the days in the calendar */
.react-datepicker__day {
  padding: 0.3rem;
  &:hover {
    background-color: #f0f0f0;
  }
}

/* Style the selected date */
.react-datepicker__day--selected {
  background-color: #0F172A;
  color: #fff;
}
.DatePicker{
  margin-top: 10px;
  display: flex;
  gap: 10px;
}


