.vaccine__card{
display: flex;
flex-direction: column;
gap: 70px;
    border-radius: 16px;
    margin-top: 30px;
    background-color: white;
    width: 350px;
    height: auto;
    padding: 20px;
}
.vaccine__card .content,
.vaccine__card .head{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.head p{
    font-size: 16px;
    font-weight: 500px;
    color: #1E293B;
}

.content p{
    font-size: 32px;
    font-weight: 600;

    color:#F34235;
}