.NotificationTable.darkTheme {
  background-color: #1e293b;
  color: #fff;
  border-color: rgba(129, 128, 128, 0.15);
}
.NotificationTable.darkTheme
  :where(.css-dev-only-do-not-override-fpg3f5)[class^="ant-table"]
  [class^="ant-table"] {
  color: #fff;
  border-color: rgba(129, 128, 128, 0.15);
  background-color: #1e293b !important;
}
.NotificationTable.darkTheme table > thead > tr:first-of-type {
  color: #fff;
  border-color: rgba(129, 128, 128, 0.15);
  background-color: #1e293b;
}
.NotificationTable.darkTheme:where(.css-dev-only-do-not-override-fpg3f5).ant-table-wrapper
  .ant-table-tbody
  > tr
  > td.ant-table-cell-row-hover {
  color: #fff;
  border-color: rgba(129, 128, 128, 0.15);
  background-color: #1e293b;
}
.NotificationTable.darkTheme:where(.css-dev-only-do-not-override-fpg3f5).ant-table-wrapper
  .ant-table-thead
  > tr
  > th:not(:last-child):not(.ant-table-selection-column):not(.ant-table-row-expand-icon-cell):not([colspan])::before {
  display: none !important;
}
