.NotificationTable,
.reportTable {
  padding: 30px;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
  border-radius: 15px;
  margin: 20px auto;
  width: 100%;
}
.NotificationTable h3,
.reportTable h3 {
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
}
.reportTable.darkTheme .NotificationTable h3,
.reportTable.darkTheme.reportTable h3 {
  color: #fff;
}
.NotificationTable p,
.reportTable p {
  font-size: 14px;
  line-height: 21px;
  font-weight: 500;
}
.ant-table-wrapper {
  margin: 30px 0;
}

.ant-table-cell .ant-tag {
  font-weight: 600;
  color: #991b1b;
  background: #fecaca;
  border-radius: 50px;
  border: none;
}

@media screen and (max-width: 900px) {
  .ant-table-wrapper {
    width: 100%;
    overflow: scroll;
  }
  .ant-table-content {
    overflow-x: scroll;
  }
}

