.donut-box {
  background-color: rgb(196, 60, 60);
  height: 50vh;
  width: 200px;
  border: 1px solid black;
}

.card-top {
  margin-top: 20px;
}

.reading-header {
  display: flex;
  justify-content: space-between;
  font-size: 16px;
}

.reading {
  background-color: #00bbd3;
  color: white;
  border-radius: 20px;
  font-size: 13px;
  height: 23px;
  width: 50px;
  padding: 3px 6px 0 8px;
}
.max-min {
  display: flex;
  gap: 20px;
  justify-content: center;
  font-size: 14px;
}

.max {
  display: flex;
  gap: 8px;
}

.min {
  display: flex;
  gap: 8px;
}
.max-heading {
  color: #dd6b20;
}
.min-heading {
  color: #f6ad55;
}

.reading-footer {
  display: flex;
  padding: 5px 0 5px 0;
  justify-content: space-between;
  font-size: 14px;
}

.temperature {
  color: #dd6b20;
  font-family: Inter;
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: 21px;
}

.hours {
  color: #64748b;
}
.battery {
  display: flex;
  gap: 10px;
  color: #64748b;
}
